import React, { useEffect, useState } from 'react';
import axios from 'axios';

const Users = () => {
    const [users, setUsers] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        // Função para buscar os usuários da API
        const fetchUsers = async () => {
            try {
                const response = await axios.get('http://localhost:8080/users/getusers');
                setUsers(response.data.Users); // Acessa a propriedade "Users" do JSON retornado
                setLoading(false);
            } catch (err) {
                setError('Erro ao carregar os usuários.');
                setLoading(false);
            }
        };

        fetchUsers();
    }, []);

    if (loading) {
        return <p>Carregando...</p>;
    }

    if (error) {
        return <p>{error}</p>;
    }

    return (
        <div>
            <h1>Lista de Usuários</h1>
            <ul>
                {users.map((user) => (
                    <li key={user.ID}>
                        <strong>Nome:</strong> {user.name}, <strong>Email:</strong> {user.Email},{' '}
                        <strong>Idade:</strong> {user.age}
                    </li>
                ))}
            </ul>
        </div>
    );
};

export default Users;